*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'montseratt', sans-serif;
}


.app{
  background-image: url('./asset/cloud.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.app.warm{
  background-image: url('./asset/sun.jpg');
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(255, 68, 68, 0.2), rgba(4, 1, 44, 0.75));
  padding: 25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;
  border-radius: 0 25px;

  background-color: rgba(99, 99, 99, 0.5);
  margin-top: -20px;
  box-shadow: 3px 5px 3px rgba(54, 1, 1, 0.3);

  color: #4dc905;
  font-size: 15px;
}

.search-box .search-bar:focus{
  background-color: rgba(255, 255, 255,0.75);
}

.location-box .location{
  color: #affc9f;
  font-size: 35px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 2.5px rgba(50, 50, 70, 0.5);
}

.location-box .date{
  color: #bafadc;
  margin-top: 5px;
  font-size: 25px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  color: #180c86;
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255,0.2);
  border-radius: 16px ;

  padding: 15px 25px;

  font-size: 105px;
  font-weight: 900;

  text-shadow: 2px 4px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow:3px 5px rgba(0, 0, 0, 0.2) ;
}

.weather-box .weather{
  color: #e1a2fa;
  font-size: 48px;
  font-weight: 700;
  box-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}